import React from 'react'
import { DateTime } from 'luxon'
import AppContext, { AppContextInterface } from '../appContext'

interface ClockState {
  date: DateTime
  tz: any
}

interface ClockProps {
  tz: any
}
export default class Clock extends React.Component<ClockProps, ClockState> {
  static contextType: React.Context<AppContextInterface> = AppContext
  timerID: any

  constructor(props: ClockProps) {
    super(props)
    this.state = {
      date: this.context && this.context.settings ? DateTime.local().setZone(this.context.settings.tz) : DateTime.local(),
      tz: this.props.tz,
    }
  }

  componentDidMount() {
    this.timerID = setInterval(() => this.tick(), 6000)
  }

  componentWillUnmount() {
    clearInterval(this.timerID)
  }

  tick = () => {
    console.log('clock.js', this.context)
    this.setState({
      date: DateTime.local().setZone(this.state.tz),
    })
  }

  render() {
    return (
      <span id="barclock">
        <p className="time">{this.state.date.toString()}</p>
      </span>
    )
  }
}
